"use client";

import dynamic from "next/dynamic";
import GoogleMapsScript from "@/app/_components/GoogleMapsScript";

const GoogleAddressSearch = dynamic(
  () => import("@/app/_components/GoogleAddressSearch"),
  { ssr: false }
);

export default function AddNewListing() {
  return (
    <>
      {/* Load Google Maps JS */}
      <GoogleMapsScript />

      <div className="p-10">
        <h2 className="font-bold text-2xl mb-2">Add New Listing</h2>

        <GoogleAddressSearch
          onSelect={(place) => {
            console.log(place);
          }}
        />
      </div>
    </>
  );
}
